import React, { useState, useMemo } from 'react';
import { ChevronDown, ChevronRight, BookOpen, Search, Filter, ArrowLeft } from 'lucide-react';

// 导入知识点数据
import nodesKnow from '../data/nodes_know.json';

const KnowledgeSystem = ({ onBackToMain }) => {
  const [expandedGroups, setExpandedGroups] = useState(new Set());
  const [searchTerm, setSearchTerm] = useState('');
  // 移除未使用的状态变量
  const [groupBy, setGroupBy] = useState('chapter'); // chapter, knowledge_type, complexity_level

  // 根据搜索词筛选知识点
  const filteredNodes = useMemo(() => {
    if (!searchTerm) return nodesKnow;
    
    const searchLower = searchTerm.toLowerCase();
    return nodesKnow.filter(node => 
      node.name.toLowerCase().includes(searchLower) ||
      node.description.toLowerCase().includes(searchLower) ||
      (node.keywords && node.keywords.some(k => k.toLowerCase().includes(searchLower))) ||
      (node.learning_path && node.learning_path.some(p => p.toLowerCase().includes(searchLower)))
    );
  }, [searchTerm]);

  // 根据分组方式组织数据
  const groupedData = useMemo(() => {
    const groups = {};
    
    filteredNodes.forEach(node => {
      let groupKey;
      let groupName;
      let groupColor;
      
      switch (groupBy) {
        case 'knowledge_type':
          groupKey = node.knowledge_type || '未分类';
          groupName = groupKey;
          groupColor = {
            '理论知识': '#8b5cf6',
            '实践知识': '#10b981',
            '专业知识': '#3b82f6',
            '商务知识': '#f59e0b',
            '未分类': '#6b7280'
          }[groupKey] || '#6b7280';
          break;
        case 'complexity_level':
          groupKey = node.complexity_level || '未分类';
          groupName = groupKey;
          groupColor = {
            '基础': '#10b981',
            '中级': '#f59e0b',
            '高级': '#ef4444',
            '未分类': '#6b7280'
          }[groupKey] || '#6b7280';
          break;
        default: // chapter
          groupKey = node.chapter || '未分类章节';
          groupName = groupKey;
          groupColor = '#8b5cf6';
          break;
      }
      
      if (!groups[groupKey]) {
        groups[groupKey] = {
          name: groupName,
          color: groupColor,
          nodes: []
        };
      }
      
      groups[groupKey].nodes.push(node);
    });

    // 章节排序逻辑：章节按顺序排在前面，其他内容按节点数量排在后面
    const groupEntries = Object.entries(groups).map(([key, group]) => ({ key, ...group }));
    
    // 分离章节和非章节
    const chapterGroups = [];
    const nonChapterGroups = [];
    
    groupEntries.forEach(group => {
      if (groupBy === 'chapter' && group.key.includes('第') && group.key.includes('章')) {
        chapterGroups.push(group);
      } else if (groupBy === 'chapter' && (group.key === '绪论' || group.key.includes('绪论'))) {
        chapterGroups.push(group);
      } else {
        nonChapterGroups.push(group);
      }
    });
    
    if (groupBy === 'chapter') {
      // 章节按顺序排序
      chapterGroups.sort((a, b) => {
        // 绪论排第一
        if (a.key.includes('绪论')) return -1;
        if (b.key.includes('绪论')) return 1;
        
        // 提取章节号进行数字排序
        const getChapterNumber = (name) => {
          const match = name.match(/第([一二三四五六七八九十\d]+)章/);
          if (match) {
            const chapterStr = match[1];
            // 处理中文数字
            const chineseNumbers = {
              '一': 1, '二': 2, '三': 3, '四': 4, '五': 5,
              '六': 6, '七': 7, '八': 8, '九': 9, '十': 10
            };
            return chineseNumbers[chapterStr] || parseInt(chapterStr) || 999;
          }
          return 999;
        };
        
        return getChapterNumber(a.key) - getChapterNumber(b.key);
      });
      
      // 非章节按节点数量排序
      nonChapterGroups.sort((a, b) => b.nodes.length - a.nodes.length);
      
      // 章节在前，其他在后
      return [...chapterGroups, ...nonChapterGroups];
    } else {
      // 非章节分组模式，按节点数量排序
      return groupEntries.sort((a, b) => b.nodes.length - a.nodes.length);
    }
  }, [filteredNodes, groupBy]);

  // 切换分组展开状态
  const toggleGroup = (groupKey) => {
    const newExpanded = new Set(expandedGroups);
    if (newExpanded.has(groupKey)) {
      newExpanded.delete(groupKey);
    } else {
      newExpanded.add(groupKey);
    }
    setExpandedGroups(newExpanded);
  };

  // 获取知识类型图标
  const getKnowledgeTypeIcon = (type) => {
    switch (type) {
      case '理论知识': return '📚';
      case '实践知识': return '🛠️';
      case '专业知识': return '🎯';
      case '商务知识': return '💼';
      default: return '📖';
    }
  };

  // 获取复杂度图标
  const getComplexityIcon = (level) => {
    switch (level) {
      case '基础': return '🌱';
      case '中级': return '🌿';
      case '高级': return '🌳';
      default: return '📝';
    }
  };

  return (
    <div className="h-screen bg-gray-900 text-white flex flex-col overflow-hidden">
      {/* 头部导航 */}
      <div className="bg-gray-800 border-b border-gray-700 px-6 py-4 flex-shrink-0">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <button
              onClick={onBackToMain}
              className="flex items-center space-x-2 px-3 py-2 rounded-lg bg-gray-700 hover:bg-gray-600 transition-colors"
            >
              <ArrowLeft className="w-4 h-4" />
              <span>返回图谱</span>
            </button>
            <div className="flex items-center space-x-3">
              <BookOpen className="w-6 h-6 text-blue-400" />
              <h1 className="text-xl font-bold">知识体系</h1>
              <span className="text-sm text-gray-400">基于 nodes_know.json</span>
            </div>
          </div>
          
          {/* 分组方式选择 */}
          <div className="flex items-center space-x-4">
            <div className="flex items-center space-x-2">
              <Filter className="w-4 h-4 text-gray-400" />
              <select
                value={groupBy}
                onChange={(e) => setGroupBy(e.target.value)}
                className="bg-gray-700 border border-gray-600 rounded px-3 py-1 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                <option value="chapter">按章节分组</option>
                <option value="knowledge_type">按知识类型分组</option>
                <option value="complexity_level">按复杂度分组</option>
              </select>
            </div>
          </div>
        </div>
      </div>

      {/* 搜索和统计 */}
      <div className="px-6 py-4 bg-gray-800/50 flex-shrink-0">
        <div className="flex items-center justify-between mb-4">
          <div className="flex-1 max-w-md">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
              <input
                type="text"
                placeholder="搜索知识点、描述、关键词..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-10 pr-4 py-2 bg-gray-700 border border-gray-600 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
          </div>
          
          {/* 统计信息 */}
          <div className="flex items-center space-x-6 text-sm">
            <div className="text-center">
              <div className="text-xl font-bold text-blue-400">{filteredNodes.length}</div>
              <div className="text-gray-400">知识点</div>
            </div>
            <div className="text-center">
              <div className="text-xl font-bold text-purple-400">{groupedData.length}</div>
              <div className="text-gray-400">分组</div>
            </div>
          </div>
        </div>
      </div>

      {/* 知识点分组展示 - 可滚动区域 */}
      <div className="flex-1 overflow-y-auto px-6 py-4">
        <div className="space-y-4 pb-6">
          {groupedData.map((group) => (
            <div key={group.key} className="bg-gray-800 rounded-lg border border-gray-700">
              {/* 分组标题 */}
              <button
                onClick={() => toggleGroup(group.key)}
                className="w-full px-4 py-3 flex items-center justify-between hover:bg-gray-700/50 transition-colors rounded-t-lg"
              >
                <div className="flex items-center space-x-3">
                  {expandedGroups.has(group.key) ? (
                    <ChevronDown className="w-4 h-4 text-gray-400" />
                  ) : (
                    <ChevronRight className="w-4 h-4 text-gray-400" />
                  )}
                  <div 
                    className="w-4 h-4 rounded-full flex-shrink-0"
                    style={{ backgroundColor: group.color }}
                  ></div>
                  <h3 className="font-medium text-left flex-1">
                    {group.name.length > 50 ? `${group.name.substring(0, 50)}...` : group.name}
                  </h3>
                </div>
                <div className="flex items-center space-x-2 flex-shrink-0">
                  <span className="text-sm text-gray-400">{group.nodes.length} 个知识点</span>
                </div>
              </button>

              {/* 分组内容 */}
              {expandedGroups.has(group.key) && (
                <div className="border-t border-gray-700">
                  <div className="p-4 space-y-3">
                    {group.nodes.map((node) => (
                      <div
                        key={node.id}
                        className="bg-gray-700/50 rounded-lg p-4 hover:bg-gray-700 transition-colors"
                      >
                        {/* 知识点标题 */}
                        <div className="flex items-start justify-between mb-2">
                          <div className="flex-1 min-w-0">
                            <h4 className="font-medium text-white mb-1 break-words">{node.name}</h4>
                            <p className="text-sm text-gray-300 leading-relaxed break-words">{node.description}</p>
                          </div>
                          <div className="flex items-center space-x-2 ml-4">
                            <span className="text-lg">
                              {getKnowledgeTypeIcon(node.knowledge_type)}
                            </span>
                            <span className="text-lg">
                              {getComplexityIcon(node.complexity_level)}
                            </span>
                          </div>
                        </div>

                        {/* 知识点属性 */}
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-3 text-xs">
                          <div className="flex flex-wrap items-center gap-2">
                            <span className="text-gray-400">知识类型:</span>
                            <span className="px-2 py-1 bg-purple-600/20 text-purple-300 rounded text-xs">
                              {node.knowledge_type || '未分类'}
                            </span>
                          </div>
                          <div className="flex flex-wrap items-center gap-2">
                            <span className="text-gray-400">复杂度:</span>
                            <span className={`px-2 py-1 rounded text-xs ${
                              node.complexity_level === '基础' ? 'bg-green-600/20 text-green-300' :
                              node.complexity_level === '中级' ? 'bg-yellow-600/20 text-yellow-300' :
                              node.complexity_level === '高级' ? 'bg-red-600/20 text-red-300' :
                              'bg-gray-600/20 text-gray-300'
                            }`}>
                              {node.complexity_level || '未分类'}
                            </span>
                          </div>
                          {node.academic_foundation && (
                            <div className="md:col-span-2">
                              <span className="text-gray-400">学术基础:</span>
                              <span className="ml-2 text-white break-words">{node.academic_foundation}</span>
                            </div>
                          )}
                          {node.learning_path && node.learning_path.length > 0 && (
                            <div className="md:col-span-2">
                              <span className="text-gray-400">学习路径:</span>
                              <div className="mt-1 flex flex-wrap gap-1">
                                {node.learning_path.map((path, idx) => (
                                  <span key={idx} className="px-2 py-1 bg-blue-600/20 text-blue-300 rounded text-xs">
                                    {path}
                                  </span>
                                ))}
                              </div>
                            </div>
                          )}
                        </div>

                        {/* 前置知识和评估标准 */}
                        {(node.prerequisites && node.prerequisites.length > 0) && (
                          <div className="mt-3 pt-3 border-t border-gray-600">
                            <div className="text-xs">
                              <span className="text-gray-400">前置知识:</span>
                              <div className="mt-1 flex flex-wrap gap-1">
                                {node.prerequisites.map((prereq, idx) => (
                                  <span
                                    key={idx}
                                    className="px-2 py-1 bg-blue-600/20 text-blue-300 rounded text-xs"
                                  >
                                    {prereq}
                                  </span>
                                ))}
                              </div>
                            </div>
                          </div>
                        )}

                        {/* 实践应用 */}
                        {node.practical_application && (
                          <div className="mt-3 pt-3 border-t border-gray-600">
                            <div className="text-xs">
                              <span className="text-gray-400">实践应用:</span>
                              <p className="mt-1 text-gray-300 leading-relaxed">{node.practical_application}</p>
                            </div>
                          </div>
                        )}
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          ))}
        </div>

        {/* 空状态 */}
        {groupedData.length === 0 && (
          <div className="text-center py-12">
            <BookOpen className="w-12 h-12 text-gray-600 mx-auto mb-4" />
            <h3 className="text-lg font-medium text-gray-400 mb-2">没有找到匹配的知识点</h3>
            <p className="text-gray-500">尝试调整搜索条件或分组方式</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default KnowledgeSystem;
